<style lang="less">
    @import '../../../../../styles/common.less';
	@import '../../modelstyle.less';
</style>
<template>
	<div>
		<Row>
			<div>
				<Card :padding="padding">
					<h4><p><Icon type="compose"></Icon></p></h4>
					<Row>
						<p>
							<Input v-model="sUnitName" placeholder="" size="small" icon="search" 
								style="width: 150px" @on-change="searching"></Input>
							&nbsp;
							<!-- <Button type="primary" @click="handleInsert()"></Button> -->
							<Button type="success" size="small" @click="handleUpdate()"></Button>
							<Button type="warning" size="small" @click="handleDelete()"></Button>
						</p>
					</Row>						
					<Row>
						<Table highlight-row border ref="dataList" :size="getFont" :height="tableHeight" 
							:columns="columns" :data="unit_list_data" :stripe="true" 
							@on-row-click="singleclick">
						</Table>
						<div style="float: right;">
							<Page :total="totalCount" :current="1" :page-size="pageSize" 
							:transfer="true" size="small"
							@on-change="changePage" @on-page-size-change="changePageSize" 
							show-total show-elevator show-sizer></Page>
						</div>
					</Row>
				</Card>
				
				
				<pageElement ref="bElement" style="padding-top: 5px"/>
				
				<!--  -->
				<Modal width="700" v-model="viewModal" title=""  ok-text="" cancel-text="" :mask-closable="false"
					@on-ok="update('updFormRef')">
					<Form ref="updFormRef" :model="viewOrUpdateModel" :rules="modelAddRules" :label-width="100">
						<FormItem label="" prop="unitCode">
							<Input v-model="viewOrUpdateModel.unitCode" placeholder="5" disabled/>
						</FormItem>
						<FormItem label="" prop="unitName">
							<Input v-model="viewOrUpdateModel.unitName" placeholder="" />
						</FormItem>
						<FormItem label="" prop="moduCode">
							<Input v-model="viewOrUpdateModel.moduCode" disabled/>
						</FormItem>
						<FormItem label="" prop="comName">
							<Input v-model="viewOrUpdateModel.comName" disabled/>
						</FormItem>
						<FormItem label="" prop="relTable">
							<Input v-model="viewOrUpdateModel.relTable" disabled/>
						</FormItem>
						<FormItem label="" prop="relColumn">
							<Select v-model="viewOrUpdateModel.relColumn" multiple>
								<Option v-for="item in colList" :value="item.value" :key="item.value">
									{{ item.label }}
								</Option>
							</Select>
						</FormItem>
						<FormItem label="" prop="relInfo">
							<Input v-model="viewOrUpdateModel.relInfo" />
						</FormItem>
					</Form>    	
				</Modal>
			</div>
		</Row>	
	</div>
</template>
<script>
import businessUnit from './businessUnit_column';
import util from '@/libs/util.js';
import datetool from '@/libs/datetool';
import Cookies from 'js-cookie';
import pageElement from '../element/pageElement-manage.vue';

	 export default {
		components: {
            pageElement
		},
        data () {
            return {
				unit_list_data: [],
				listurl: '/business/TK0005L.do',
				saveurl: '/business/TK0005I.do',
				deleteurl: '/business/TK0005D.do',
				updateurl: '/business/TK0005U.do',
				selecturl: '/business/TK0005S.do', 	
				collisturl: '/business/TK0005S1.do',
				pageSize: 10,
				currentPage: 1,
				totalCount: 0,
				totalPage: 0,
				orderFileds: [],
				sUnitCode: '',
				sModuCode: '',
				sUnitName: '',
				modelAddRules: {
					unitCode : [{required: true}],
					unitName : [{required: true}],
					relTable : [{required: true}],
					relColumn : [{required: true}]
				},
				deletedPks: '',
				selectedLines: 0,
				viewOrUpdateModel: {},
				viewModal: false,
				colList: [],
				columns: [],
				tableHeight: 200,
				padding: 2,
				index: -1
			};
			
		},
		
		methods: {
		
			getSearchCond() {
				return {'menuCode': '', 'pageSize': this.pageSize, 'currentPage': this.currentPage, 
					'valObj': {'unitCode': this.sUnitCode, 'unitName': this.sUnitName, 'moduCode': this.sModuCode}
				};
			},
		
			//	
			init() {
				businessUnit.setPage(this);
				this.columns = businessUnit.getColumns();
			},
			
			//() ()
			getUnitDataList(data){
				this.sModuCode = data;
				businessUnit.page(this.getSearchCond());
				
				//
				this.deletedPks = '';
				this.selectedLines = 0;
				this.$refs.bElement.getElementDataList('-1');
			},
			
			changePage(page) {
				let cond = this.getSearchCond();
				cond.currentPage = page;
				businessUnit.page(cond);
			},
			
			changePageSize(_pageSize) {
				let cond = this.getSearchCond();
				cond.pageSize = _pageSize;
				businessUnit.page(cond);        	
			},
			searching() {
				businessUnit.page(this.getSearchCond());
			},
			
			//
			handleDelete () {
				businessUnit.delete(this.deleteurl+"?unitCode="+this.deletedPks);
			},
			
			//
			handleUpdate () {
				if(this.index == -1) {
					this.$Modal.warning({
						title: '',
						content: ''
					});
					
					return;
				};
				
				this.viewModal = true;
				
				var params = new URLSearchParams();
				params.append('relTable', this.viewOrUpdateModel.relTable);
				businessUnit.getColList(params);
			},
			
			//
			update (name) {
				this.viewOrUpdateModel.updDate = datetool.format(new Date());
				
				//Array","
				this.viewOrUpdateModel.relColumn = this.viewOrUpdateModel.relColumn.join(',');
				businessUnit.update(name);
			},
			
			singleclick(row, index){
				this.index = index;
				this.viewOrUpdateModel = row;
				this.viewOrUpdateModel.relColumn = this.viewOrUpdateModel.relColumn.split(',');
				
				this.deletedPks = row.unitCode;
				this.$refs.bElement.getElementDataList(this.deletedPks);
			}
		},
		created () {
			this.init();
		},
		computed:{
			//
			getFont(){
				const sizeValue=Cookies.get("sizeValue");
				const size=this.$store.state.app.sizeFont;
				if(!sizeValue){
					return size;
				}else{
					return sizeValue;
				}
			}
		},
		
		mounted() {
			this.tableHeight = (window.innerHeight - this.$refs.dataList.$el.offsetTop - 280)/2
		},
	};
</script>


